@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsans.woff2") format("woff2"), url("../fonts/ptsans.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "PT Sans";
  src: url("../fonts/ptsansbold.woff2") format("woff2"), url("../fonts/ptsansbold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
html {
  background-color: #f2f2f2;
  position: relative;
}

body {
  margin: 0;
  font-family: "PT Sans", "Arial", sans-serif;
  text-transform: uppercase;
  background-color: #ffffff;
}

@media (min-width: 1200px) {
  body {
    width: 1200px;
    margin: 0 auto;
    box-shadow: -13px 0 13px 0 rgba(219, 219, 219, 0.6), 13px 0 13px 0 rgba(219, 219, 219, 0.6);
  }
}
