  text-align: center;
}

.booking__title {
  width: 270px;
  margin: 0 auto;
  margin-bottom: 19px;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  color: #000000;
  text-align: center;
}

.booking__text {
  width: 270px;
  margin: 0 auto;
  margin-bottom: 35px;
  font-size: 14px;
  line-height: 24px;
  font-weight: 400;
  color: #333333;
  text-align: center;
}

@media (min-width: 768px) {
  .booking {
    width: 768px;
    margin: 0 auto;
    padding-top: 55px;
