    margin-bottom: 31px;
  }
  .booking__text {
    width: 450px;
    margin-bottom: 42px;
  }
}
@media (min-width: 1200px) {
  .booking {
    padding-bottom: 66px;
  }
  .booking__text {
    margin-bottom: 47px;
  }
}
.button {
  box-sizing: border-box;
  display: inline-block;
  vertical-align: top;
  text-align: center;
  border: none;
  padding: 16px;
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  color: #ffffff;
  text-decoration: none;
  text-transform: uppercase;
  background-color: #81b3d2;
}

.button:not(.button--disabled):hover {
  background-color: #669ec0;
}

.button:not(.button--disabled):active {
  background-color: #5496bd;
  color: rgba(255, 255, 255, 0.3);
}

.button--disabled {
  background-color: #e5e5e5;
}

.button--booking {
  width: 280px;
}

.button--submit {
  width: 280px;
}

.button--ok {
  width: 180px;
  margin: 0 auto;
  display: block;
  font-size: 18px;
}

.button--close {
  width: 280px;
  margin: 0 auto;
  display: block;
  font-size: 18px;
}

@media (min-width: 768px) {
  .button--booking {
    width: 458px;
  }
  .button--submit {
    width: 324px;
  }
  .button--ok {
    width: 272px;
  }
  .button--close {
    width: 458px;
    font-size: 21px;
  }
}
@media (min-width: 1200px) {
  .button--submit {
    width: 480px;
  }
}
.copyright {
  width: 129px;
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
}

.copyright__text {
  margin: 0;
  font-size: 14px;
  line-height: 26px;
  font-weight: 400;
  color: #000000;
  margin-top: 3px;
}

.copyright__link svg {
  width: 27px;
  height: 34px;
  fill: #000000;
}

.copyright__link:hover svg {
  fill: #81b3d2;
}

.copyright__link:active svg {
  fill: #b2b2b2;
}

