  }
  .input-block--contacts:last-of-type {
    margin-bottom: 0;
  }
  .input-block--contacts .input-block__label {
    margin-bottom: 7px;
  }
  .input-block--contacts .input-block__field {
    width: 648px;
  }
  .input-block--attractions {
    margin-bottom: 30px;
  }
  .input-block--attractions .input-block__label {
    width: 122px;
    margin-right: 60px;
  }
  .input-block--attractions:nth-of-type(2n) .input-block__label {
    margin-right: 0;
  }
}
@media (min-width: 1200px) {
  .input-block--impressions {
    margin-bottom: 23px;
  }
  .input-block--impressions .input-block__label::before {
    left: 4px;
  }
  .input-block--impressions .input-block__label {
    padding-left: 80px;
    width: 248px;
  }
  .input-block__label--impressions::after {
    content: "";
    width: 22px;
    height: 22px;
    position: absolute;
    top: 18px;
    left: 21px;
    border-radius: 50%;
    background-color: #333333;
    display: none;
  }
  .input-block__field[id=firstname] {
    width: 350px;
  }
  .input-block__field[id=lastname] {
    width: 312px;
  }
  .input-block__field[id=patronymic] {
    width: 314px;
  }
  .input-block--contacts {
    margin-bottom: 0;
  }
  .input-block--contacts span {
    display: none;
  }
  .input-block--contacts:nth-of-type(1) {
    width: 400px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  .input-block--contacts:nth-of-type(2) {
    width: 480px;
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
  }
  .input-block--contacts .input-block__label {
    padding: 0;
    margin-top: 18px;
  }
  .input-block__label[for=tel]::before,
  .input-block__label[for=email]::before {
    position: absolute;
    content: "";
    display: block;
    width: 58px;
    height: 58px;
    top: -18px;
    right: -330px;
  }
  .input-block__label[for=email]::before {
    right: -334px;
  }
  .input-block--contacts .input-block__field {
    width: 315px;
  }
  .input-block--attractions {
    margin-right: 68px;
    margin-bottom: 20px;
  }
  .input-block--attractions:nth-of-type(4n) {
    margin-right: 0;
  }
  .input-block--attractions .input-block__label {
    padding-left: 80px;
    margin-right: 0;
    width: auto;
  }
}
